import { Component, OnInit } from '@angular/core';
import {DataService} from '../../services/data.service';

@Component({
  selector: 'app-robot',
  templateUrl: './robot.component.html',
  styleUrls: ['./robot.component.css']
})
export class RobotComponent implements OnInit {
  robots: Robot[];
  robot: Robot;
  constructor(public dataService: DataService) { }

  ngOnInit() {
    this.robots = [];
    this.initRobotForCreate();
    this.displayAllRobots();
  }
  addRobot() {
    this.robots.unshift(this.robot);
    this.dataService.saveRobot(this.robot).subscribe(
      res => {
        console.log(res);
        this.displayAllRobots();
      },
      err => {
        console.log('Error occured');
      }
  );
    this.initRobotForCreate();
    this.displayAllRobots();
  }

  initRobotForCreate() {
    this.robot = {
      id: 0,
      name: 'NAME',
      description: 'DESC',
      quantity: 0
    };
  }

  displayAllRobots() {
    this.dataService.getAllRobots().subscribe((robots) => {
      this.robots = robots;
      }
    );
  }

}

interface Robot {
  id: number;
  name: string;
  description: string;
  quantity: number;
}



